@import './variables';

/*HP 4 sections*/

.four-main-section {
  background-color: #eee;
}

.four-main {
  display: flex;
  flex-wrap: wrap;

  .section {
    width: calc(50% - 2 * var(--pad));
    /*padding: var(--pad);*/
    margin: var(--pad);
    /*border: 1px solid grey;*/

    @media (--semilarge-screen) {
      width: calc(100% - 2 * var(--pad));
    }

    &::before {
      content: 'ahoj';
      display: block;
      height: 240px;
      margin-left: var(--pad);
      background-color: #ddd;
    }

    &.why-us {
      h2 { background-color: var(--whyUsColor); }

      .button {
        border-color: var(--whyUsColor);
        color: var(--whyUsColor);

        &:hover {
          background-color: var(--whyUsColor);
        }
      }
    }

    &.way-we-teach {
      h2 { background-color: var(--wayWeTeachColor); }

      .button {
        border-color: var(--wayWeTeachColor);
        color: var(--wayWeTeachColor);

        &:hover {
          background-color: var(--wayWeTeachColor);
        }
      }
    }

    &.daily-life {
      h2 { background-color: var(--dailyLifeColor); }

      .button {
        border-color: var(--dailyLifeColor);
        color: var(--dailyLifeColor);

        &:hover {
          background-color: var(--dailyLifeColor);
        }
      }
    }

    &.our-results {
      h2 { background-color: var(--ourResultsColor); }

      .button {
        border-color: var(--ourResultsColor);
        color: var(--ourResultsColor);

        &:hover {
          background-color: var(--ourResultsColor);
        }
      }
    }

    h2 {
      text-align: center;
      background-color: #abc;
      margin: 0;
      margin-top: -80px;
      margin-right: var(--pad);
      padding: 8px var(--pad);
      /*background-color: #999;*/
      color: white;
      font-size: 1.25rem;
      letter-spacing: 0.05rem;
    }

    div {
      text-align: center;
      margin-right: var(--pad);
      padding: calc(var(--pad) * 1.5) calc(var(--pad) * 1.5) calc(var(--pad) * 3);
      background-color: white;
    }

    p {
      /*text-align: center;*/
      padding-bottom: calc(var(--pad) * 1.5);
    }

    a.button {
      /*margin: 0 auto;*/
      /*border: 1px solid grey;*/
      text-decoration: none;
      border: 1px solid white;
      padding: 3px 16px 5px;
      border-radius: 3px;

      &:hover {
        color: white;
        border: 0;
      }
    }
  }
}
